body {
  font-family: $font-main;
  & > * {
    color: rgba(0, 0, 0, 0.87);
  }
}

hr {
  width: 100%;
}

.font-medium{
  font-size: 16px;
}
.font-big{
  font-size: 21px;
  p,h4,h5,h6,.p,.h4,.h5,.h6, a,button,td.table-tbody_td,li.list-item, a.footer-nav-link, .aboute-site-company{
    font-size: 24px !important;
  }
  [class*='col-']{
    padding: 0 10px;
  }
  .row{
    margin: 0 -10px;
  }
  .container{
    padding: 0 10px;
  }
  .social-button{

    margin: .2rem;
   }
  // @media (max-width:1441px) {
  //   font-size: 16px;
  // }
}
.social-block{
  flex-wrap: wrap;
}
.social-button{
 height: cr(36px);
 line-height: 1;
}
.card-question{
  height: 100%;
}
.card-question.with-collapse{
  height: auto;
}
.book,.post-container{
  &:hover{
    z-index: 50;
    .post{
      height: 100%;
    }
  }
 
}
.flex-grow-05{
  flex-grow: 0.5;
}
.card-category .card-body .card-img{
  margin-left: 0;
}
.header-with-scroll{
  .search-and-nav{
    .icon-search{
      font-size: cr(18px);
    }
    .form-control{
      height: cr(45px);
      top: cr(5px);
    }
  }
}
.btn-lg{
  height: 60px;
  border-radius: 30px;
}
.btn,.btn-md{
  height: 50px;
  border-radius: 25px;
}
.btn-sm{
  height: 40px;
  border-radius: 20px;
}
#glosary .alphabet .btn-outline-primary{
  width: 50px;
  height: 50px;
}